:root {
  --mescuot_primary: #fb923c;
  --mescuot_primary-hover: #f97316;
  --mescuot_primary-light: #fff7ed;
  --mescuot_primary-light-hover: #ffedd5;
  --mescuot_primary-dark: #c2410c;

  --mescuot_secondary: #6B7280;
  --mescuot_secondary-hover: #4B5563;

  --mescuot_success: #10B981;
  --mescuot_success-hover: #059669;
  --mescuot_success-light: #D1FAE5;
  --mescuot_danger: #EF4444;
  --mescuot_warning: #F59E0B;

  --mescuot_white: #FFFFFF;
  --mescuot_gray-50: #F9FAFB;
  --mescuot_gray-100: #F3F4F6;
  --mescuot_gray-200: #E5E7EB;
  --mescuot_gray-300: #D1D5DB;
  --mescuot_gray-400: #9CA3AF;
  --mescuot_gray-500: #6B7280;
  --mescuot_gray-600: #4B5563;
  --mescuot_gray-700: #374151;
  --mescuot_gray-800: #1F2937;
  --mescuot_gray-900: #111827;

  --mescuot_border-radius: 8px;
  --mescuot_border-radius-lg: 12px;

  --mescuot_shadow-sm: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
  --mescuot_shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  --mescuot_shadow-md: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  --mescuot_shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  --mescuot_shadow-xl: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
}

.mescuot_overlay {
  position: fixed;
  inset: 0;
  background-color: rgba(0,0,0,.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  padding: 1rem;
  animation: mescuot_fadeIn .3s ease-out;
  backdrop-filter: blur(8px);
  overflow-y: auto;
}
@keyframes mescuot_fadeIn {
  from { opacity: 0; backdrop-filter: blur(0); }
  to { opacity: 1; backdrop-filter: blur(8px); }
}

.mescuot_contenido {
  background-color: var(--mescuot_white);
  width: 100%;
  max-width: 560px;
  max-height: 90vh;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  border-radius: var(--mescuot_border-radius-lg);
  box-shadow: var(--mescuot_shadow-xl);
  margin: auto;
  position: relative;
}

.mescuot_header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
  padding: 1rem;
  background: linear-gradient(135deg, var(--mescuot_primary) 0%, var(--mescuot_primary-hover) 100%);
  border-bottom: 1px solid rgba(255,255,255,.15);
  color: white;
  position: sticky;
  top: 0;
  z-index: 10;
}
.mescuot_header-left {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  flex: 1;
  min-width: 0;
}
.mescuot_icon-circle {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  background-color: rgba(255,255,255,.2);
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--mescuot_white);
  flex-shrink: 0;
}
.mescuot_header-texts {
  display: flex;
  flex-direction: column;
  gap: .1rem;
  min-width: 0;
}
.mescuot_title {
  margin: 0;
  font-size: 1.1rem;
  font-weight: 700;
  color: #fff;
  letter-spacing: .2px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.mescuot_subtitle {
  margin: 0;
  font-size: 0.75rem;
  font-weight: 500;
  color: rgba(255,255,255,.9);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.mescuot_close-btn {
  background: none;
  border: none;
  padding: .25rem;
  cursor: pointer;
  color: var(--mescuot_white);
  transition: all .2s;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 8px;
  width: 32px;
  height: 32px;
  flex-shrink: 0;
}
.mescuot_close-btn:hover { background-color: rgba(255,255,255,.14); }
.mescuot_close-btn:disabled { opacity: .5; cursor: not-allowed; }

.mescuot_body {
  padding: 1rem 1rem 0;
  background-color: var(--mescuot_white);
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  flex: 1;
}

.mescuot_periodos-section { margin-top: .5rem; }
.mescuot_section-header {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  margin-bottom: 1rem;
}
.mescuot_section-header-actions {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  gap: 0.5rem;
  width: 100%;
}
.mescuot_section-title {
  font-size: .8rem;
  font-weight: 700;
  color: var(--mescuot_gray-700);
  text-transform: uppercase;
  letter-spacing: .05em;
  margin: 0;
}

.mescuot_selection-info {
  font-size: 0.8rem;
  color: var(--mescuot_gray-500);
  font-weight: 500;
  text-align: right;
  white-space: nowrap;
}

.mescuot_periodos-grid-container {
  min-height: auto;
  margin-bottom: 0;
  padding-bottom: 0;
}
.mescuot_periodos-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(140px, 1fr));
  gap: .5rem;
  align-content: flex-start;
  margin-bottom: 0;
}
.mescuot_periodo-card {
  display: flex;
  align-items: center;
  padding: 0.75rem;
  border-radius: var(--mescuot_border-radius);
  background-color: var(--mescuot_white);
  border: 1px solid var(--mescuot_gray-200);
  cursor: pointer;
  transition: all .2s;
  position: relative;
  overflow: hidden;
  min-height: 54px;
}
.mescuot_periodo-card:hover {
  border-color: var(--mescuot_primary);
  box-shadow: var(--mescuot_shadow-sm);
}
.mescuot_periodo-card.mescuot_seleccionado {
  background-color: var(--mescuot_primary-light);
  border-color: var(--mescuot_primary);
}
.mescuot_periodo-checkbox {
  position: relative;
  margin-right: .5rem;
  z-index: 1;
}
.mescuot_periodo-checkbox input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}
.mescuot_checkmark {
  position: relative;
  height: 1rem;
  width: 1rem;
  background-color: var(--mescuot_white);
  border: 1px solid var(--mescuot_gray-300);
  border-radius: 4px;
  transition: all .2s;
}
.mescuot_periodo-card:hover .mescuot_checkmark { border-color: var(--mescuot_primary); }
.mescuot_periodo-checkbox input:checked ~ .mescuot_checkmark {
  background-color: var(--mescuot_primary);
  border-color: var(--mescuot_primary);
}
.mescuot_checkmark:after {
  content: "";
  position: absolute;
  display: none;
  left: 5px;
  top: 2px;
  width: 3px;
  height: 6px;
  border: solid white;
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
}
.mescuot_periodo-checkbox input:checked ~ .mescuot_checkmark:after { display: block; }
.mescuot_periodo-label {
  flex-grow: 1;
  font-size: .85rem;
  font-weight: 600;
  color: var(--mescuot_gray-800);
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 1;
  gap: 0.5rem;
  text-transform: uppercase;
}

.mescuot_footer {
  padding: 1rem;
  background-color: var(--mescuot_white);
  border-top: 1px solid var(--mescuot_gray-200);
  position: sticky;
  bottom: 0;
  z-index: 5;
}
.mescuot_footer-sides {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}
.mescuot_footer-left {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: .5rem;
  order: 2;
}
.mescuot_footer-right {
  display: flex;
  align-items: center;
  gap: .5rem;
  order: 1;
}

.mescuot_btn {
  padding: .75rem;
  border-radius: var(--mescuot_border-radius);
  font-size: .85rem;
  font-weight: 700;
  cursor: pointer;
  transition: all .2s;
  border: 1px solid transparent;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  flex: 1;
  min-width: 0;
}
.mescuot_btn-small {
  padding: .35rem .5rem;
  font-size: .7rem;
  min-width: auto;
}

.mescuot_btn-terciario {
  background-color: var(--mescuot_primary-light);
  color: var(--mescuot_primary);
  border: 1px solid var(--mescuot_primary);
}
.mescuot_btn-terciario:hover:not(:disabled) {
  background-color: var(--mescuot_primary-light-hover);
}

.mescuot_btn-primary {
  background-color: var(--mescuot_primary);
  color: var(--mescuot_white);
  box-shadow: var(--mescuot_shadow-sm);
}
.mescuot_btn-primary:hover:not(:disabled) {
  background-color: var(--mescuot_primary-hover);
  box-shadow: var(--mescuot_shadow);
}
.mescuot_btn-primary:disabled {
  background-color: var(--mescuot_gray-300);
  cursor: not-allowed;
  box-shadow: none;
}

.mescuot_btn-secondary {
  background-color: var(--mescuot_white);
  color: var(--mescuot_gray-800);
  border-color: var(--mescuot_gray-300);
  font-weight: 700;
}
.mescuot_btn-secondary:hover:not(:disabled) {
  background-color: var(--mescuot_gray-50);
  border-color: var(--mescuot_gray-400);
}

/* Media Queries */
@media (min-width: 400px) {
  .mescuot_header { padding: 1rem 1.25rem; }
  .mescuot_body { padding: 1.25rem 1.25rem 0; }
  .mescuot_footer { padding: 1rem 1.25rem; }
  .mescuot_periodos-grid {
    grid-template-columns: repeat(auto-fill, minmax(160px, 1fr));
    gap: .75rem;
  }
}

@media (min-width: 500px) {
  .mescuot_footer-sides {
    flex-direction: row;
    justify-content: space-between;
  }
  .mescuot_footer-left { order: 1; justify-content: flex-start; }
  .mescuot_footer-right { order: 2; justify-content: flex-end; align-items: flex-end;}
  .mescuot_btn { flex: 0 1 auto; min-width: 120px; }
  .mescuot_section-header { flex-direction: row; align-items: center; justify-content: space-between; }
  .mescuot_section-header-actions { justify-content: flex-end; width: auto; }
}

@media (min-width: 600px) {
  .mescuot_header{margin: 0;}
  .mescuot_contenido { max-height: 85vh; margin: 0; }
  .mescuot_header { padding: 1.25rem 1.5rem; }
  .mescuot_icon-circle { width: 42px; height: 42px; }
  .mescuot_title { font-size: 1.3rem; }
  .mescuot_subtitle { font-size: 0.875rem; }
  .mescuot_body { padding: 1.5rem 1.5rem 0; }
  .mescuot_periodos-grid { grid-template-columns: repeat(auto-fill, minmax(180px, 1fr)); }
}

@media (max-width: 768px) {
  .mescuot_section-header-actions{width: fit-content;}
  .mescuot_section-header{flex-direction: row; justify-content: space-between;}
  .mescuot_btn{flex: none;}
  .mescuot_footer-left{width: fit-content;}
  .mescuot_footer-sides{flex-direction: row; justify-content: space-between;}
  .mescuot_footer-left{order: 0;}
}

/* Badge contador que se ve solo en móvil */
.mescuot_mobile-count {
  display: none;
  margin-left: .5rem;
  background: rgba(255,255,255,.2);
  color: #fff;
  font-size: .75rem;
  font-weight: 800;
  line-height: 1;
  padding: .25rem .45rem;
  border-radius: 9999px;
}

/* Botones de acción con icono + texto (desktop) */
.mescuot_action-btn {
  gap: .5rem;
}

/* ===== Vista móvil (≤480px): 2 columnas, contenedor más bajo ===== */
@media (max-width: 480px) {
  .mescuot_periodos-grid {
    grid-template-columns: repeat(2, 1fr) !important;
    gap: .5rem !important;
  }

  .mescuot_contenido {
    max-height: 80vh; /* más bajo */
  }

  .mescuot_body {
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    padding-bottom: 12px; /* mismo margen que en web, no tanto espacio extra */
  }

  .mescuot_periodo-card {
    padding: .6rem;
    min-height: 48px;
  }
  .mescuot_periodo-label {
    font-size: .8rem;
  }

  .mescuot_mobile-count { display: inline-flex; align-items: center; }

  .mescuot_action-btn {
    width: 44px;
    height: 44px;
    padding: 0;
    justify-content: center;
  }
  .mescuot_action-btn .btn-label {
    position: absolute !important;
    width: 1px; height: 1px;
    padding: 0; margin: -1px;
    overflow: hidden; clip: rect(0,0,0,0);
    white-space: nowrap; border: 0;
  }

  .mescuot_footer-right {
    gap: .5rem;
  }
}

/* ===== Web (≥769px): 3 columnas fijas + separador inferior ===== */
@media (min-width: 769px) {
  .mescuot_periodos-grid {
    grid-template-columns: repeat(3, 1fr) !important;
  }
  .mescuot_periodos-grid::after {
    content: "";
    display: block;
    height: 12px;
    grid-column: 1 / -1;
  }
}
