:root {
  --emp-primary: #2563EB;
  --emp-primary-light: #3B82F6;
  --emp-primary-dark: #1D4ED8;
  --emp-secondary: #7C3AED;
  --emp-accent: #EC4899;
  --emp-success: #10B981;
  --emp-warning: #F59E0B;
  --emp-danger: #EF4444;
  --emp-light: #F8FAFC;
  --emp-dark: #0F172A;
  --emp-gray-100: #F1F5F9;
  --emp-gray-200: #E2E8F0;
  --emp-gray-300: #CBD5E1;
  --emp-gray-400: #94A3B8;
  --emp-gray-500: #64748B;
  --emp-gray-600: #475569;
  --emp-gray-700: #334155;
  --emp-gray-800: #1E293B;
  --emp-gray-900: #0F172A;

  --emp-border-radius: 12px;
  --emp-border-radius-sm: 8px;
  --emp-border-radius-lg: 16px;
  --emp-border-radius-xl: 20px;
  --emp-transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  --emp-shadow-sm: 0 1px 3px rgba(0, 0, 0, 0.05);
  --emp-shadow-md: 0 10px 18px rgba(0,0,0,0.12);
  --emp-shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  --emp-shadow-xl: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  --emp-glass: rgba(255, 255, 255, 0.95);
  --emp-glass-border: rgba(255, 255, 255, 0.2);
}

@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&family=Plus+Jakarta+Sans:wght@500;600;700&display=swap');

/* ===========================
   CONTENEDOR
=========================== */
.emp-baja-container {
  position: relative;
  width: 100%;
  min-height: 100vh;
  padding: 2rem;
  background: #f3f4f6;
  border: none !important;
  border-radius: 0 !important;
  box-shadow: none !important;
  backdrop-filter: none !important;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
}
.emp-baja-glass {
  position: absolute;
  top: 0; left: 0; right: 0;
  height: 100px;
  color: white;
  background: linear-gradient(to right, var(--emp-danger), var(--emp-accent));
  z-index: 0;
  border: none !important;
  border-radius: 0 !important;
  box-shadow: none !important;
  backdrop-filter: none;
}

/* ===========================
   BARRA SUPERIOR
=========================== */
.emp-baja-barra-superior {
  position: relative;
  z-index: 1;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: 1.5rem;
  padding-bottom: 1.5rem;
  margin-bottom: 1rem;
}
.emp-baja-titulo-container { min-width: 200px; text-align: left; position: relative; z-index: 1; }
.emp-baja-titulo {
  font-family: 'Plus Jakarta Sans', sans-serif;
  font-size: 1.8rem; font-weight: 700; color: white; margin: 0;
  background: linear-gradient(90deg, var(--emp-danger), var(--emp-primary-dark));
  -webkit-background-clip: text; background-clip: text;
}
.emp-baja-boton-volver {
  padding: 0.55rem 0.85rem;
  border-radius: 10px;
  font-family: 'Inter', sans-serif; font-weight: 600;
  cursor: pointer; transition: var(--emp-transition);
  display: inline-flex;align-items: center; gap: 0.45rem;
  border: 1px solid var(--emp-gray-300);
  background-color: var(--emp-glass);
  color: var(--emp-dark);
  box-shadow: var(--emp-shadow-sm);
  position: relative; z-index: 1;
  font-size: .92rem;
}
.emp-baja-boton-volver{ display: none}
.emp-baja-boton-volver:hover { background-color: var(--emp-danger); color: white; border-color: var(--emp-danger); }

/* ===========================
   BUSCADOR
=========================== */
.emp-baja-buscador-container { position: relative; margin: 0 1rem; }
.emp-baja-buscador {
  width: 100%; padding: 0.75rem 1rem 0.75rem 2.5rem;
  border-radius: var(--emp-border-radius);
  border: 1px solid var(--emp-gray-300);
  font-family: 'Inter', sans-serif; font-size: 0.95rem;
  color: var(--emp-gray-700); background-color: var(--emp-light);
  transition: var(--emp-transition); box-shadow: var(--emp-shadow-sm);
}
.emp-baja-buscador:focus { outline: none; border-color: var(--emp-danger); box-shadow: 0 0 0 3px rgba(239, 68, 68, 0.2); }
.emp-baja-buscador-icono { position: absolute; left: 1rem; top: 50%; transform: translateY(-50%); color: var(--emp-gray-400); }

/* ===========================
   TABLA (DESKTOP / WEB)
=========================== */
.emp-baja-tabla-body::-webkit-scrollbar { width: 6px; height: 6px; }
.emp-baja-tabla-body::-webkit-scrollbar-track { background: rgba(241, 245, 249, 0.5); border-radius: 10px; }
.emp-baja-tabla-body::-webkit-scrollbar-thumb { background: rgba(100, 116, 139, 0.4); border-radius: 10px; }
.emp-baja-tabla-body::-webkit-scrollbar-thumb:hover { background: rgba(100, 116, 139, 0.6); }

.emp-baja-tabla-body {
  max-height: 50vh; overflow: auto;
  scrollbar-width: thin; scrollbar-color: rgba(100,116,139,0.4) rgba(241,245,249,0.5);
}
.emp-baja-tabla-container {
  position: relative; margin: 0 1rem; flex: 1; max-height: 70vh;
  border-radius: var(--emp-border-radius); overflow: hidden;
  backdrop-filter: blur(5px); display: flex; flex-direction: column;
  border-bottom: 2px solid var(--emp-danger); background: transparent;
}
.emp-baja-tabla-header-container { display: flex; flex-direction: column; gap: 7px; }
.emp-baja-tabla-header {
  display: grid; border-radius: 14px 14px 0 0;
  grid-template-columns: 60px 1.8fr 1fr 1.2fr 120px;
  background: linear-gradient(135deg, var(--emp-danger), var(--emp-accent));
  color: white; font-weight: 600; font-size: 0.9rem;
  padding: 12px 0; backdrop-filter: blur(5px);
  position: sticky; top: 0; z-index: 2;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
}
.emp-baja-tabla-header > div,
.emp-baja-fila > div { padding: 0 8px; display: flex; align-items: center; justify-content: center; }

.emp-baja-fila {
  display: grid;
  grid-template-columns: 60px 1.8fr 1fr 1.2fr 120px;
  padding: 10px 0; border-bottom: 1px solid rgba(237, 242, 247, 0.7);
  align-items: center; background: #ffffff;
  position: relative; font-size: 0.9rem;
}
.emp-baja-fila:nth-child(even) { background: #f7f7f7; }

.emp-baja-col-motivo--click {
  cursor: pointer;
  text-decoration: underline dotted;
  text-underline-offset: 2px;
}

.emp-baja-iconos { display: flex; justify-content: center; align-items: center; gap: 6px; font-size: 1.3rem; }
.emp-baja-icono {
  color: var(--emp-danger); transition: var(--emp-transition); cursor: pointer;
  font-size: 1.9rem; padding: 5px; border-radius: 10px; background-color: rgba(239, 68, 68, 0.1);
}
.emp-baja-icono:hover {
  transform: scale(1.2); color: white; background-color: var(--emp-danger);
  box-shadow: 0 2px 8px rgba(239, 68, 68, 0.3);
}
.emp-baja-icono-danger { background-color: rgba(239,68,68,.12); }
.emp-baja-disabled { opacity: 0.5; pointer-events: none; }

/* ===========================
   EMPTY STATE
=========================== */
.emp-baja-tabla-container:has(.emp-baja-sin-resultados) { background: transparent; border: none; border-bottom: 2px solid var(--emp-danger); }
.emp-baja-tabla-container:has(.emp-baja-sin-resultados) .emp-baja-tabla-header { border-bottom: 1px solid var(--emp-gray-200); }
.emp-baja-tabla-body:has(.emp-baja-sin-resultados) {
  background: #ffffff; padding: 16px 0; border: 1px solid var(--emp-gray-200);
  border-top: none; border-radius: 0 0 var(--emp-border-radius) var(--emp-border-radius);
}
.emp-baja-contador { font-size: 0.9rem; color: var(--emp-gray-600); margin: 1rem 0 0.5rem; padding: 0 0.5rem; background: transparent !important; }
.emp-baja-contador strong { color: var(--emp-danger); margin-left: 0.25rem; }
.emp-baja-sin-resultados {
  padding: 30px; color: var(--emp-gray-600); font-size: 1rem; font-weight: 500;
  text-align: center; background-color: #ffffff; border-radius: var(--emp-border-radius);
  box-shadow: var(--emp-shadow-sm); border: 1px solid var(--emp-gray-200);
  display: flex; flex-direction: column; align-items: center; justify-content: center;
  max-width: 300px; margin: 0 auto;
}
.emp-baja-sin-icono { font-size: 2rem; margin-bottom: 1rem; opacity: 0.5; color: var(--emp-gray-500); }
.emp-baja-cargando { padding: 20px; text-align: center; color: var(--emp-gray-600); font-size: 0.9rem; }

/* ===========================
   MODALES
=========================== */
.emp-baja-modal-overlay {
  position: fixed; inset: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex; justify-content: center; align-items: center;
  z-index: 1000; backdrop-filter: blur(5px);
  animation: empb-fadeIn .2s ease-out;
}
.emp-baja-modal {
  background-color: white;
  padding: 2rem;
  border-radius: var(--emp-border-radius-lg);
  box-shadow: var(--emp-shadow-xl);
  max-width: 500px; width: 90%;
  text-align: center; position: relative;
  border-top: 4px solid transparent;
  animation: empb-pop .18s ease-out;
}
.emp-baja-modal--success { border-top-color: var(--emp-success); }
.emp-baja-modal--danger  { border-top-color: var(--emp-danger); }
/* Nuevo: variante informativa (motivo) */
.emp-baja-modal--info    { border-top-color: var(--emp-primary); }

.emp-baja-modal__icon { font-size: 2.5rem; color: var(--emp-success); margin-bottom: 1rem; }
.emp-baja-modal__icon--danger { color: var(--emp-danger); }
/* Nuevo: icono info en primario */
.emp-baja-modal__icon--info { color: var(--emp-primary); }

.emp-baja-modal__title { margin: 0 0 1rem; font-size: 1.4rem; font-weight: 600; color: var(--emp-success); }
.emp-baja-modal__title--danger  { color: var(--emp-danger) !important; }
.emp-baja-modal__title--success { color: var(--emp-success) !important; }
/* Nuevo: título info en primario */
.emp-baja-modal__title--info { color: var(--emp-primary) !important; }

.emp-baja-modal__body { color: var(--emp-gray-700); line-height: 1.5; margin: 0 0 1rem; }
.emp-baja-modal__body strong { color: var(--emp-dark); }
.emp-baja-modal__body--scroll { max-height: 40vh; overflow: auto; text-align: left; white-space: pre-wrap; }

.emp-baja-modal__actions { display: flex; gap: 1rem; justify-content: center; margin-top: 1rem; }
.emp-baja-btn {
  flex: 1; max-width: 220px;
  padding: .75rem 1.25rem;
  border-radius: var(--emp-border-radius);
  font-weight: 600; cursor: pointer;
  transition: var(--emp-transition);
  border: 1px solid transparent;
}
.emp-baja-btn--ghost { background: #fff; color: var(--emp-gray-700); border-color: var(--emp-gray-300); }
.emp-baja-btn--ghost:hover { background: var(--emp-gray-100); transform: translateY(-2px); box-shadow: var(--emp-shadow-md); }
.emp-baja-btn--solid-success { background: var(--emp-success); color: #fff; border-color: var(--emp-success); }
.emp-baja-btn--solid-success:hover { filter: brightness(.95); transform: translateY(-2px); box-shadow: var(--emp-shadow-md); }
.emp-baja-btn--solid-danger { background: var(--emp-danger); color: #fff; border-color: var(--emp-danger); }
.emp-baja-btn--solid-danger:hover { filter: brightness(.95); transform: translateY(-2px); box-shadow: var(--emp-shadow-md); }
.emp-baja-btn:disabled { opacity: .7; cursor: not-allowed; transform: none !important; box-shadow: none !important; }

@keyframes empb-fadeIn { from { opacity: 0 } to { opacity: 1 } }
@keyframes empb-pop { from { transform: translateY(4px); opacity: .98 } to { transform: translateY(0); opacity: 1 } }

/* ===========================
   RESPONSIVE
=========================== */
@media (max-width: 992px) {
  .emp-baja-container { padding: 1.5rem; }
  .emp-baja-titulo { font-size: 1.6rem; }
  .emp-baja-tabla-header, .emp-baja-fila {
    grid-template-columns: 60px 1.6fr 1fr 1.1fr 110px;
  }
}
.arriva{display: block;}
/* ===== MOBILE (≤768px) ===== */
@media (max-width: 768px) {

  html, body {
  margin: 0;          /* <- elimina la franja */
  padding: 0;
}

  .emp-baja-boton-volver{ display: inline-flex}
  .arriva{display: none;}
  .emp-baja-buscador-container{ margin: 0; }
  .emp-baja-barra-superior { position: static; margin-bottom: 3rem; margin-top: 1rem; }
  .emp-baja-tabla-container{  margin:0;margin-top: 1rem; border-bottom: none; }

  /* Ocultar botón superior y dejar espacio para la barra inferior */
  .emp-baja-barra-superior .emp-baja-boton-volver { display: none; }
  .emp-baja-container { padding-bottom: 90px; } /* evita que la barra tape contenido */

  .emp-baja-tabla-header { display: none; }
  .emp-baja-tabla-body { max-height: 50vh; overflow: auto; }

  .emp-baja-fila {
    display: block;
    position: relative;
    background: #ffffff;
    border-radius: 12px;
    padding: 12px 12px 10px 14px;
    margin: 10px 0;
    box-shadow: var(--emp-shadow-sm);
    border: 1px solid var(--emp-gray-200);
  }
  .emp-baja-fila::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0; bottom: 0;
    width: 4px;
    border-radius: 6px 0 0 6px;
    background: linear-gradient(180deg, var(--emp-danger), var(--emp-accent));
  }

  .emp-baja-fila > div {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 8px;
    padding: 3px 6px;
    border-bottom: 1px dashed rgba(226, 232, 240, 0.9);
    text-align: right;
    grid-template-columns: unset;
  }
  .emp-baja-fila > div:last-child {
    border-bottom: none;
    padding-bottom: 2px;
  }

  .emp-baja-col-id::before      { content: "ID"; }
  .emp-baja-col-nombre::before  { content: "Razón Social"; }
  .emp-baja-col-fecha::before   { content: "Fecha de baja"; }
  .emp-baja-col-motivo::before  { content: "Motivo"; }
  .emp-baja-col-acciones::before{ content: "Acciones"; }

  .emp-baja-fila > div::before {
    font-weight: 600;
    color: var(--emp-gray-700);
    opacity: 0.95;
    min-width: 130px;
    text-align: left;
    margin-right: 8px;
    flex: 0 0 auto;
  }

  .emp-baja-col-id,
  .emp-baja-col-nombre,
  .emp-baja-col-fecha,
  .emp-baja-col-motivo,
  .emp-baja-col-acciones {
    justify-content: flex-end;
    text-align: right;
    width: 100%;
  }

  .emp-baja-col-nombre,
  .emp-baja-col-fecha,
  .emp-baja-col-motivo {
    white-space: normal;
    overflow: visible;
    text-overflow: initial;
    text-transform: none;
    color: var(--emp-dark);
  }

  .emp-baja-col-acciones { padding-top: 10px; border-bottom: none !important; }
  .emp-baja-iconos { justify-content: flex-end; gap: 12px; font-size: 1.4rem; width: 100%; }

  .emp-baja-modal__actions { flex-direction: row; flex-wrap: wrap; }
  .emp-baja-btn { min-width: 120px; max-width: calc(50% - .5rem); }

  .emp-baja-sin-resultados { padding: 20px; font-size: 0.9rem; }

  /* ===== Barra inferior fija (móvil) ===== */
  .emp-baja-navbar-mobile {
    position: fixed;
    bottom: 0; left: 0; right: 0;
    height: 60px;
    background: #fff;
    border-top: 1px solid var(--emp-gray-200);
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding: 0.5rem 1rem;
    box-shadow: var(--emp-shadow-sm);
    z-index: 999;
  }

  .emp-baja-navbar-mobile .emp-baja-boton-volver {
    max-width: 280px;
    text-align: center;
    justify-content: center;
    font-size: 1rem;
    border-radius: var(--emp-border-radius);
    border: 2px solid var(--emp-danger);
    color: var(--emp-danger);
    background-color: #fff;
    font-weight: 600;
    box-shadow: var(--emp-shadow-sm);
  }

  .emp-baja-navbar-mobile .emp-baja-boton-volver:hover {
    background: var(--emp-danger);
    color: #fff;
    border-color: var(--emp-danger);
  }
}

@media (max-width: 576px) {
  .emp-baja-container { padding: 1rem; }
  .emp-baja-titulo {
    font-size: 1.4rem;
    text-align: center;
    width: 100%;
  }
  .emp-baja-titulo-container {
    display: flex;
    justify-content: center;
    width: 100%;
  }

  .emp-baja-fila > div { max-width: 100%; grid-template-columns: unset; }
}
